<template>
    <div id='foot'>
        <ul>
          <li id="left">
            <span>客服电话： 177-805-820-83 （服务时间 7：00-18：30）</span>
            <span id="wx" :style="wx"></span>
            <span id="xl" :style="xl"></span>
          </li>
          <li id="right">
              <span><a href="#">中文版</a></span>
              <span>|</span>
              <span><a href="#">English</a></span>
              <span>|</span>
              <span><a href="#">联系我们</a></span>
              <span>|</span>
              <span><a href="#">用户协议</a></span>
          </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "Foot",
      data:function (){
        return {

          wx:{
            backgroundImage:"url("+require("../assets/wx.png") +")",
            backgroundRepeat:'no-repeat',
            backgroundSize:"100% 100%"
          },
          xl:{
            backgroundImage:"url("+require("../assets/xina.png") +")",
            backgroundRepeat:'no-repeat',
            backgroundSize:"100% 100%"
          }
        }
      }
    }
</script>

<style scoped lang="less">

    #foot{
      width: 100%;
      height: 40px;
      font-size: 10px;
      ul{
        width: 1400px;
        margin: 0 auto;
        li{
          height: 25px;
          padding: 5px 0px;
          #wx,#xl{
            display: inline-block;
            width: 20px;
            height: 20px;
          }
          a:hover{
            text-decoration: underline;
          }
          span{
            margin: 0 5px;
          }
        }
        #left{
          float: left;
        }
        #right{
          float: right;
          margin-right: 100px;
        }


        li::before{
          content: '';
          clear: both;
        }
      }
    }

</style>



